/* product home */

.hovereffect {
    width: 100%;
    height: 300px;
    float: left;
    overflow: hidden;
    position: relative;
    text-align: center;
    cursor: default;
  }
  
  .hovereffect .overlay {
    width: 100%;
    height: 300px;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.6);
    opacity: 0;
    filter: alpha(opacity=0);
    transform: translate(460px, -100px) rotate(180deg);
    transition: all 0.2s 0.4s ease-in-out;
  }
  
  .hovereffect img {
    display: block;
    height: 300px;
    width: 100%;
    position: relative;
    transition: all 0.2s ease-in;
  }
  
  .hovereffect h2 {
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    position: relative;
    font-size: 17px;
    padding: 10px;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .hovereffect a.info {
    display: inline-block;
    text-decoration: none;
    padding: 7px 14px;
    text-transform: uppercase;
    color: #fff;
    border: 1px solid #fff;
    margin: 50px 0 0 0;
    background-color: transparent;
    transform: translateY(-200px);
    transition: all 0.2s ease-in-out;
  }
  
  .hovereffect a.info:hover {
    box-shadow: 0 0 5px #fff;
  }
  
  .hovereffect:hover .overlay {
    opacity: 1;
    filter: alpha(opacity=100);
    transition-delay: 0s;
    transform: translate(0px, 0px);
  }
  
  .hovereffect:hover h2 {
    transform: translateY(0px);
    transition-delay: 0.5s;
  }
  
  .hovereffect:hover a.info {
    transform: translateY(0px);
    transition-delay: 0.3s;
  }
.followHovereffect {
    width: 100%;
    /* height: 100%; */
    float: left;
    overflow: hidden;
    position: relative;
    text-align: center;
    cursor: default;
    }

    .followHovereffect .overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
    }

    .followHovereffect img {
    display: block;
    position: relative;
    }

    .followHovereffect h2 {
    text-transform: uppercase;
    color: white;
    text-align: center;
    font-size: 25px;
    }

    .followHovereffect:hover h2 {
    opacity: 0;
    filter: alpha(opacity=0);
    transform: translate3d(-50%,-50%,0) scale3d(0.8,0.8,1);
    }

    .followHovereffect a.info {
    display: inline-block;
    text-decoration: none;
    padding: 7px 14px;
    text-transform: uppercase;
    color: #fff;
    border: 1px solid #fff;
    margin: 50px 0 0 0;
    background-color: transparent;
    }

    .followHovereffect a.info:hover {
    box-shadow: 0 0 5px #fff;
    }

    .followHovereffect h2,
    .followHovereffect p {
    position: absolute;
    top: 50%;
    left: 50%;
    transition: opacity 0.35s, transform 0.35s;
    transform: translate3d(-50%,-50%,0);
    transform-origin: 50%;
    background-color: transparent;
    margin: 0px;
    padding: 0px;
    }

    .followHovereffect .overlay:before {
    position: absolute;
    /* top: 50%;
    left: 50%;
    width: 10%;
    height: 20%; */
    border: 2px solid #fff;
    content: '';
    opacity: 0;
    filter: alpha(opacity=0);
    transition: opacity 0.35s, transform 0.35s;
    transform: translate3d(-50%,-50%,0) rotate3d(0,0,1,-45deg) scale3d(0,0,1);
    transform-origin: 50%;
    }

    .followHovereffect p {
    width: 20%;
    text-transform: none;
    font-size: 15px;
    line-height: 2;
    }

    .followHovereffect p a {
    color: rgb(224, 11, 11);
    }

    .followHovereffect p a:hover,
    .followHovereffect p a:focus {
    opacity: 0.6;
    filter: alpha(opacity=60);
    }

    .followHovereffect  a i {
    opacity: 0;
    filter: alpha(opacity=0);
    transition: opacity 0.35s, transform 0.35s;
    padding: 0px 5px;
    }

    .followHovereffect p a:first-child i {
    transform: translate3d(-60px,-60px,0);
    }

    .followHovereffect p a:nth-child(2) i {
    transform: translate3d(60px,-60px,0);
    }

    .followHovereffect p a:nth-child(3) i {
    transform: translate3d(-60px,60px,0);
    }

    .followHovereffect p a:nth-child(4) i {
    transform: translate3d(60px,60px,0);
    }

    .followHovereffect:hover .overlay:before {
    opacity: 1;
    filter: alpha(opacity=100);
    background-color: rgba(0,0,0,0.2);
    transform: translate3d(-50%,-50%,0) rotate3d(0,0,1,-135deg) scale3d(1,1,1);
    }

    .followHovereffect:hover p i:empty {
    transform: translate3d(0,0,0);
    opacity: 1;
    filter: alpha(opacity=100);
    }
.zoom {
    position: fixed;
    bottom: 45px;
    right: 24px;
    height: 70px;
    }

    .zoom-fab {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    background-color: #009688;
    vertical-align: middle;
    text-decoration: none;
    text-align: center;
    transition: 0.2s ease-out;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    color: #FFF;
    }

    .zoom-fab:hover {
    background-color: #4db6ac;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
    }

    .zoom-btn-large {
    width: 60px;
    height: 60px;
    line-height: 60px;
    }

    .zoom-menu {
    position: absolute;
    right: 70px;
    left: auto;
    top: 50%;
    transform: translateY(-50%);
    height: 100%;
    width: 500px;
    list-style: none;
    text-align: right;
    }

    .zoom-menu li {
    display: inline-block;
    margin-right: 10px;
    }

    .zoom-card {
    position: absolute;
    right: 150px;
    bottom: 70px;
    transition: box-shadow 0.25s;
    padding: 24px;
    border-radius: 2px;
    background-color: #009688;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    color: #FFF;
    }

    .zoom-card ul {
    -webkit-padding-start: 0;
    list-style: none;
    text-align: left;
    }

    .zoom-btn-person { background-color: #F44336; }

    .zoom-btn-person:hover { background-color: #e57373; }

    .zoom-btn-doc { background-color: #ffeb3b; }

    .zoom-btn-doc:hover { background-color: #fff176; }

    .zoom-btn-tangram { background-color: #4CAF50; }

    .zoom-btn-tangram:hover { background-color: #81c784; }

    .zoom-btn-report { background-color: #2196F3; }

    .zoom-btn-report:hover { background-color: #64b5f6; }

    .zoom-btn-feedback { background-color: #9c27b0; }

    .zoom-btn-feedback:hover { background-color: #ba68c8; }


    .scale-transition { transition: transform 0.3s cubic-bezier(0.53, 0.01, 0.36, 1.63) !important; }

    .scale-transition.scale-out {
    transform: scale(0);
    transition: transform 0.2s !important;
    }

    .scale-transition.scale-in { transform: scale(1); }
